/** Scss stylesheet file with general style properties */

body {
  font-size: 13.8px;
  font-weight: 400;
  padding: 0;
  margin: 0;
  line-height: 1.614;
  background: #fff;
}

* {
  font-family: Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

